@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap);
* {
  box-sizing: border-box;
}

html {
  font-family: "Noto Sans JP", sans-serif;
}

@media all and (max-width: 493px) {
  body {
    margin: 0;
    height: 100%;
    background-color: #f1f1f1;
  }
}
@media all and (min-width: 493px) {
  body {
    margin: 0;
    -webkit-align-items: center;
            align-items: center;
    background-color: #f1f1f1;
  }
}

.logo{
    margin-top: 5%;
    text-align: center;
}
form {
  padding-bottom: 20px;
  position: relative;
  text-align: center;
}

label {
  display: block;
  text-align: left;
  margin-left: 9%;
  margin-bottom: 1%;
  color: #c0c0c0;
  font-style: normal;
  font-size: 10px;
  font-weight: bold;
}

input {
  margin-bottom: 3%;
  background: #5b5b5b;
  padding: 12px;
  border-style: none;
  outline: none;
  border-radius: 3px;
  width: 83%;
  height: 39px;
  color: #f1f1f1;
}

input:focus {
  border-color: #7cb9e8;
  box-shadow: 0 0 6px 0 #7cb9e8;
}

.login {
  border: none;
  background: none;
}

.submit button {
  width: 83%;
  height: 42px;
  margin-top: 2%;
  background: #318ce7;
  border-style: none;
  border-radius: 3px;
  color: #d3d3d3;
  cursor: pointer;
  font-size: medium;
  text-align: center;
  font-weight: bold;
}

.login {
  display: block;
  text-align: left;
  margin-left: 9%;
  margin-top: 1.5%;
  font-size: small;
  font-weight: 20px;
}

.login span {
  color: #989898;
  font-size: 12px;
  padding-right: 1%;
  font-style: normal;
  cursor: default;
}

@media all and (max-width: 493px) {
  .outline {
    background-color: #303030;
    height: 100%;
  }

  .sign {
    padding-top: 4%;
    padding-bottom: 4%;
    color: #f4f4ff;
    text-align: center;
  }

  .login-head {
    padding-top: 4%;
    padding-bottom: 4%;
    color: #f4f4ff;
    text-align: center;
  }
}

@media all and (min-width: 493px) {
  .outline {
    background-color: #303030;
    margin-top: 5%;
    margin-left: 31%;
    margin-right: 30%;
    margin-bottom: 5%;
    border-radius: 9px;
    padding-bottom: 1%;
  }

  .sign {
    padding-top: 4%;
    padding-bottom: 4%;
    color: #f4f4ff;
    text-align: center;
  }

  .login-head {
    padding-top: 4%;
    padding-bottom: 4%;
    color: #f4f4ff;
    text-align: center;
  }
}

